:root {
  --size-stdSpace: 10px;
}

.movie {
  display: flex;
  flex-direction: column;
  /* width: 45%; */
  margin: 50px 2.5%;
  background-color: #fff;
  height: 100%;
}
.movie_poster {
  width: 300px;
}
.movie_title {
  margin-left: var(--size-stdSpace);
}
.movie_genres {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-left: var(--size-stdSpace);
}
.movie_genre {
  margin-right: 10px;
  font-size: 13px;
  color: #afafaf;
}
.movie_rating {
  margin-left: var(--size-stdSpace);
}
.movie_summary {
  margin: 0 var(--size-stdSpace);
  padding-bottom: 20px;
}

@media (max-width: 1000px) {
  .movie_title {
    display: inline-block;
  }
}
